import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform         } from 'ionic-angular';
import { Developments } from '../../providers';
import { InAppBrowser} from '@ionic-native/in-app-browser';
import { Api, User } from '../../providers';
import { DocumentViewer  } from '@ionic-native/document-viewer';
import { File } from '@ionic-native/file';
import { FileTransfer } from '@ionic-native/file-transfer';
import { CallNumber } from '@ionic-native/call-number';

@IonicPage()
@Component({
  selector: 'page-documents',
  templateUrl: 'documents.html',
})

export class DocumentsPage {

  btnPage = false;
  btnCall = false;
  view: { prices: { show: boolean, icon: string }, mp: { show: boolean, icon: string }, distribution: { show: boolean, icon: string }, blueprints:{ show: boolean, icon: string },       
          brochureEs:{ show: boolean, icon: string }, brochureEn: { show: boolean, icon: string }, advertising: { show: boolean, icon: string }, gallery: { show: boolean, icon: string },
          
          icon : { up : string , down : string} 
        } 
      = 
        { prices:  { show: false, icon : 'ios-arrow-forward-outline'}, mp:  { show: false, icon: 'ios-arrow-forward-outline' }, distribution:  { show: false, icon: 'ios-arrow-forward-outline' }, blueprints: { show: false, icon: 'ios-arrow-forward-outline' },
          brochureEs: { show: false, icon: 'ios-arrow-forward-outline' }, brochureEn:   { show: false, icon: 'ios-arrow-forward-outline' },   advertising:  { show: false, icon: 'ios-arrow-forward-outline' },   gallery:   { show: false, icon: 'ios-arrow-forward-outline' },
      icon: { up: 'ios-arrow-down-outline', down: 'ios-arrow-forward-outline' }
        };
  constructor(private callNumber: CallNumber,public platform: Platform, public file : File, public transfer : FileTransfer, public document: DocumentViewer, public api : Api, public user : User,  public developments : Developments, public iab : InAppBrowser, public navCtrl: NavController, public navParams: NavParams) {
    ////console.log("documents");
    ////console.log(this.view);
    ////console.log(this.developments);
  }
 
  ionViewDidLoad() {

  }

  buttonClick(n){    

    n.show = !n.show;

    if (n.icon == this.view.icon.down){
      n.icon = this.view.icon.up;
    }else{
      n.icon = this.view.icon.down;
    }    

  }

  openBrochure(url){ 

    this.api.get('?lang=esp&source=app&mode=true&development_number='+ this.developments.development.folio + '&userid=' + this.user.userInfo.userid , 'http://broknet.immomexico.com/brochuregenerator.php').subscribe(data => {

        var options = "location=yes,hidden=yes,beforeload=yes";    

      this.iab.create(data.json().url,'_system', options);

    
    

    });


  } 

  call(number, fullname) {


    this.callNumber.callNumber(number, true)
      .then(res => this.btnCall = false)
      .catch(err => this.btnCall = false);

  }

  openPage2(url) {
    var options = "location=yes,hidden=yes";
    
  this.iab.create(url,'_system', options);

  

   

  }
}



